import Flipbook from "flipbook-vue";
export default {
  name: "Home",
  components: { Flipbook },
  data: function() {
    return {
      end: false,
      bats: [],
      batsAnswers: ["dark"],
      garden: [],
      gardenAnswers: ["compost"],
      environment: [],
      environmentAnswers: ["solar"],
      questions: [[null, null, null, null], 
                  [null, null], 
                  [null, null, null, null], 
                  [null, null, null, null, null, null, null, null],
                  [null, null, null, null, null, null, null, null, null],
                  [null], 
                  [null], 
                  [null, null], 
                  [null, null, null, null, null, null],
                  [null, null, null, null]],
      pages: [
        null,
        "0.png",
        "1.png",
        "2.png",
        "3.png",
        "4.png",
        "5.png",
        "6.png",
        "7.png",
        "8.png",
        "9.png",
        "10.png",
        "11.png",
        "12.png",
        "13.png",
        "14.png"
      ],
      ui: true,
      grabbing: false,
      page: 0,
      flipbook: null
    };
  },
  methods: {
    onFlipLeftEnd(page) {
      this.page = page;
      this.ui = true;
    },
    hideUI() {
      this.ui = false;
    },
    onFlipRightEnd(page) {
      this.page = page;
      if(this.page == 15) {
        this.$bvModal.show("modal-1");
        this.end = true;
      }
      this.ui = true;
    },
    checkGrab() {
      this.grabbing = this.flipbook.cursor == "grabbing";
    }
  },
  mounted() {
    window.addEventListener("mousemove", this.checkGrab);
    window.addEventListener("touchend", this.checkGrab);
    window.addEventListener("touchmove", this.checkGrab);
    this.flipbook = this.$refs["flipbook"];
  }
};
</script>

